* {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}


:root {
  /* --caution300: #D2051E; */
  --primary100: #AAC0FA;
  --primary300: #0050C9;
  --primary500: rgb(16, 0, 202);
  --highlight: #FFFB95;

  --my-font: Verdana, Geneva, Tahoma, sans-serif;

  --secondary100: #FFDEB8;

  --success100: #C5F6DF;

  --neutral100: rgb(200, 200, 200);

  --personalityShadow: rgba(255, 0, 191, 0.25);

  --greenHighlight: rgba(0, 255, 119, 0.417);
  --purpleHighlight: rgba(114, 67, 255, 0.25);

  --cardShadow: rgba(255, 0, 191, 0.40)
}

h1 {
  font-size: 4.1em;
}

h2 {
  font-size: 1.1em;
  font-weight: 400;
}

h3 {
  font-size: 1.5em;
  /* background-color: purple; */
  margin: 0;
  font-weight: 600;
}

.App {
  background-color: white;
  background:
    linear-gradient(0deg, rgb(16, 0, 202, .1), rgb(255, 255, 255)),
    url("noise.svg");
}


/* Navigation Bar */
.NavBar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: white;
  box-shadow: 1px 2px 5px var(--personalityShadow);
  padding: 0 1em;
}

.NavBarLogo {}

.NavTabs {
  display: flex;
  flex-direction: row;
}

.NavBar button {
  margin: 1.5em 1em;
  color: black;
  background-color: white;
  text-decoration: none;
  border: none;
  padding: .5em 1em;
  border-radius: 1em;
  box-shadow: 1px 2px 5px var(--personalityShadow);
  font-size: 16px;
}

.NavBar button:hover {
  box-shadow: inset 1px 3px 5px var(--personalityShadow);
}





/* Body */
.Body {
  padding: 0 15em;
  margin: 0 0 10em;
  /* filter: contrast(170%) brightness(1000%); */

  /* background-color: white */
}



/* Card */
.ProjectCards {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  /* background-color: red; */
}

.Body button {
  /* margin: 1em auto; */
  margin: 0 0 2em 0;
  padding: 2em;
  width: 47%;
  text-decoration: none;
  color: black;
  border: none;
  border-radius: 1em;
  box-shadow: 2px 4px 10px var(--cardShadow);
  /* background-color: yellow; */
}

.Body button:hover {
  box-shadow: inset 2px 4px 10px var(--cardShadow);
}


.Card {
  background-color: white;
}

.CardTitle {
  font-size: 2em;
  margin: .5em 0 0em .5em;
  text-align: left;
}

.CardDescription {
  margin: 0 1em;
  text-align: left;
}

.CardImage img {
  width: auto;
  height: auto;
  max-height: 400px;
  max-width: 100%;
}

.CardTags {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.CardTag {
  margin: .2em 1em;
  background-color: var(--purpleHighlight);
  border-radius: 1em;
  /* border: 1px solid var(--primary500); */
  /* margin: .2em; */
  padding: .2em .6em .35em .6em;
}


/* Footer */
.Footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: white;
  font-size: 1.2em;
  box-shadow: -1px -2px 5px var(--personalityShadow);
  padding: 1em 0;
  margin: 5em 0 0 0;
}




/* Initial Page and Cards */
.LandingPageName {
  margin: 10em 0 5em 0;
}

.LandingPageBlurb {
  font-size: 2em;
  font-weight: 200;
  margin: 0 0 6em 0;
}

.LandingPageButtons {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  /* background-color: red; */
}

.LandingPageButtons button {
  padding: .5em;
  width: 25%;
  margin: 1em;
  border-width: 1em;
  /* background-color: blue; */
}

.PinkHighlight {
  background-color: var(--personalityShadow);
  border-radius: 1em;
  padding: .01em .5em .15em .5em;
}

.GreenHighlight {
  background-color: var(--greenHighlight);
  border-radius: 1em;
  padding: .01em .5em .15em .5em;
}

.PurpleHighlight {
  background-color: var(--purpleHighlight);
  border-radius: 1em;
  padding: .01em .5em .15em .5em;
}






/* Project Page */
.PersonasProject {
  /* background-color: purple; */
}

.ProjectTitle {
  margin: 100px 0 0 0;
  /* font-size: 32px; */
}

.ProjectSectionContainer {
  /* background-color: blue; */
  display: flex;
  flex-direction: column;

  margin: 3em 0;
  /* box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.25); */
  border-radius: 12px;
  padding: 2em;
  border-style: solid;
  border-color: var(--primary500);
  background-color: white;
}

.FirstLevelRow {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 0 2.8em 0;
  /* background-color: blue; */
}



.FirstLevelName {
  font-size: 2.5em;
  font-style: italic;
  font-weight: 500;
  width: 25%;
  padding: 12px;
  /* background-color: red; */
}


.FirstLevelBox {
  width: 70%;
  border-radius: 12px;
  padding: 18px 12px 12px 0;
  font-size: 1.4em;
  /* font-size: 20px; */
  /* background-color: red; */
}

.FirstLevelBox ol {
  margin: 0;
}

.FirstLevelBox img {
  width: 100%;
  height: auto;
}

.SecondLevelRow {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 1em 0;
  /* background-color: purple; */
}

.SecondLevelName {
  font-size: 18px;
  font-size: 1.4em;
  text-decoration: solid;
  text-decoration-style: solid;
  width: 25%;
  padding: 12px;
  /* margin: .5em 0 0 .5em; */
  /* background-color: pink; */
}

.SecondLevelBox {
  width: 70%;
  padding: 0 12px 12px 0;
  font-size: 1.2em;
  /* background-color: pink; */
  /* margin: 2em; */
}


.SecondLevelBox img {
  width: auto;
  height: auto;
  /* border: 1px solid black; */
  border-radius: 1em;
  max-height: 1200px;
  max-width: 100%;
}

.SecondLevelBox button {
  width: 98%;
  /* margin: 1em 1em; */
  padding: 1em;
  text-decoration: none;
  color: black;
  border: none;
  border-radius: 1em;
  box-shadow: 2px 4px 10px var(--cardShadow);
  background-color: white;

}

.SecondLevelBox ul {
  margin: .5em 0 2em 0;
  /* padding: 0; */
}

.SecondLevelBox ul li {
  margin: 0;
}

.SecondLevelBox ul li ul {
  margin: .25em 0 .41em 0;
}

.SecondLevelBox ol li {
  margin: 0;
}

.SecondLevelBoxOrderedList {
  font-weight: 700;
  background-color: var(--primary100);
  padding: 4px 8px;
  border-radius: 1em;
  /* border-style: solid;
  border-width: 1px;
  border-color: black; */
}

.SecondLevelBox li a {
  margin: 0 0 0 1em;
}


mark {
  background-color: var(--highlight);
  padding: 6px 12px;
  border-radius: 1em;
  border-style: solid;
  border-color: black;
  /* box-shadow: inset 1px 3px 5px rgba(0, 0, 0, 0.25); */
}





/* About Page */
.AboutPagePicAndBlurb {
  display: flex;
  flex-direction: row;
  font-size: 1.1em;
}

.AboutPagePicAndBlurb h2 {
  font-size: 1.5em;
  font-weight: 600;
}

.AboutPagePic {
  background-color: red;
  /* width: 50%; */
}

.AboutPagePicAndBlurb img {
  max-width: 40%;
  margin: 0 4.1em 0 0;
  border-radius: 1em;
}

.AboutPageBlurb {
  width: 55%;
  font-size: 18px;
}